import { useEffect, useRef, useCallback } from 'react';
export default function useDebounce(cb, duration) {
  const timer = useRef(null);

  const clearTimer = useCallback(
    () => timer.current && clearTimeout(timer.current),
    []
  );
  const setTimer = useCallback(
    (cb) => (timer.current = setTimeout(cb, duration)),
    [duration]
  );

  useEffect(() => {
    return () => clearTimer();
  }, [clearTimer]);

  const debounced = useCallback(
    (...args) => {
      clearTimer();
      setTimer(() => cb(...args));
    },
    [clearTimer, setTimer, cb]
  );

  return debounced;
}
