import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import IconButton from '@material-ui/core/IconButton';

import Paper from './Paper';
import Input from './Input';
import Select from './Select';
import { useTranslation } from '../providers/TranslationProvider';
import AutoComplete from './AutoComplete';

import ClearIcon from '@material-ui/icons/Clear';

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(),
    overflow: 'visible',
  },
}));

function AddressForm(props) {
  const {
    errors,
    values,
    cities,
    countries,
    disabled,
    layout,
    fetchingCities,
    onChange,
    onSubmit,
    onClear,
    onLoadMoreCities,
    handleCitySearchChange,
  } = props;
  const classes = useStyles();
  const { t } = useTranslation();

  const addressEl = (
    <Input
      name="address"
      label={t('Address')}
      value={values.address}
      error={errors.address}
      onChange={onChange}
      disabled={disabled}
      required
    />
  );

  const addressComplementEl = (
    <Input
      name="addressComplement"
      label={t('Complement')}
      value={values.addressComplement}
      onChange={onChange}
      disabled={disabled}
    />
  );

  const countryEl = (
    <Select
      name="country"
      label={t('Country')}
      value={values.country}
      options={countries}
      error={errors.country}
      onChange={onChange}
      disabled={disabled}
      required
    />
  );

  const cityEl = (
    <Box display="flex" alignItems="center">
      <AutoComplete
        name="city"
        label={t('City')}
        value={values.city}
        options={cities}
        error={errors.city}
        onChange={onChange}
        handleInputSearch={handleCitySearchChange}
        disabled={disabled}
        onLoadMore={onLoadMoreCities}
        loading={fetchingCities}
        disableClearable
      />
      {values?.city && (
        <Box mt={1} mb={0.5} ml={0.5}>
          <IconButton size="small" onClick={() => onClear('city')}>
            <ClearIcon fontSize="small" />
          </IconButton>
        </Box>
      )}
    </Box>
  );

  const zipCodeEl = (
    <Input
      name="zipCode"
      label={t('Zip code')}
      value={values.zipCode}
      error={errors.zipCode}
      onChange={onChange}
      disabled={disabled}
    />
  );

  const components = {
    address: addressEl,
    addressComplement: addressComplementEl,
    country: countryEl,
    city: cityEl,
    zipCode: zipCodeEl,
  };

  return (
    <Paper className={classes.paper}>
      <form onSubmit={onSubmit} noValidate>
        <Grid container spacing={2} justify="center">
          {layout.map(({ span, component }) => (
            <Grid item xs={span} key={component}>
              {components[component]}
            </Grid>
          ))}
          <Grid item xs={12} align="right">
            <Button type="submit" disabled={disabled}>
              {t('Save')}
            </Button>
          </Grid>
        </Grid>
      </form>
    </Paper>
  );
}

AddressForm.defaultProps = {
  errors: {},
  values: {},
  cities: [],
  layout: [],
  onChange: () => {},
  onSubmit: () => {},
};

export default AddressForm;
