import React, { useState, useCallback, useEffect, useMemo } from 'react';
import { Grid } from '@material-ui/core';
import { useFormik } from 'formik';
import { useSnackbar } from 'notistack';

import EshopAPI from '../EshopAPI';
import AddressForm from '../components/AddressForm';
import Loader from '../components/Loader';
import { useTranslation } from '../providers/TranslationProvider';
import { useEshop } from '../providers/EshopProvider';
import useInfiniteLoading from '../hooks/useInfiniteLoading';
import useDebounce from '../hooks/useDebounce';

function useCitites(options) {
  const {
    data: cities,
    total,
    page,
    fetching,
    fetchData,
    loadMore,
  } = useInfiniteLoading({
    fetchAPI: EshopAPI.fetchCities,
    ...options,
  });

  return useMemo(
    () => ({
      cities,
      totalCities: total,
      page,
      fetchingCities: fetching,
      fetchCities: fetchData,
      loadMoreCities: loadMore,
    }),
    [cities, total, page, fetching, fetchData, loadMore]
  );
}
function AddressFormContainer(props) {
  const { redirectToAddressList, match, history, location } = props;
  const { config, setInvoicingAddress, setDeliveryAddress } = useEshop();
  const { params } = match;
  const { addressId } = params;

  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [countries, setCountries] = useState([]);
  const [validateOnChange, setValidateOnChange] = useState(false);
  const [address, setAddress] = useState({
    name: '',
    personName: '',
    addressComplement: '',
    address: '',
    city: '',
    zipCode: '',
    country: '',
    id: undefined,
  });
  const [citySearchInput, setCitySearchInput] = useState();
  const { cities, fetchingCities, fetchCities, loadMoreCities } = useCitites();
  const debouncedFetchCities = useDebounce(fetchCities, 1000);

  const handleRedirectAfterSave = useCallback(() => {
    const fromUrl = location && location.state && location.state.fromUrl;
    if (fromUrl) {
      history.replace(fromUrl);
    } else {
      redirectToAddressList();
    }
  }, [location, history, redirectToAddressList]);

  async function handleSubmitAddress(address) {
    setSaving(true);
    const service = address.id
      ? EshopAPI.updateAddress(address.id, address)
      : EshopAPI.addAddress(address);

    service
      .then((address) => {
        if (address.id) {
          setInvoicingAddress(address.id);
          setDeliveryAddress(address.id);
        }
      })
      .then(() => {
        setSaving(false);
        handleRedirectAfterSave();
      })
      .catch(() => {
        enqueueSnackbar(t('Error'), { variant: 'error' });
        setSaving(false);
      });
  }

  useEffect(() => {
    if (!addressId) {
      // new address
      setLoading(false);
    } else {
      EshopAPI.fetchAddress(addressId)
        .then((address) => {
          setAddress(address);
          setLoading(false);
        })
        .catch(redirectToAddressList);
    }
  }, [addressId, redirectToAddressList]);

  const {
    values,
    errors,
    handleChange: handleChangeFormik,
    handleSubmit: handleSubmitFormik,
    setValues,
    setFieldValue,
  } = useFormik({
    validateOnChange,
    initialValues: {
      name: address.name,
      personName: address.personName,
      addressComplement: address.addressComplement,
      address: address.address,
      city: address.city,
      zipCode: address.zipCode,
      country: address.country,
      id: address.id,
    },
    validate: (values) => {
      const errors = {};

      if (!values.address) {
        errors.address = `${t('Address')} ${t('Required')}`;
      }
      if (!values.country) {
        errors.country = `${t('Country')} ${t('Required')}`;
      }

      return errors;
    },
    onSubmit: async (values) => {
      await handleSubmitAddress(values);
    },
    enableReinitialize: true,
  });

  const { t } = useTranslation();

  const handleSubmit = useCallback(
    (e) => {
      e.preventDefault();
      setValidateOnChange(true);
      handleSubmitFormik();
    },
    [handleSubmitFormik]
  );

  const handleClear = useCallback(
    (name) => {
      setFieldValue(name, '');
      if (name === 'city') {
        setCitySearchInput('');
        setFieldValue('zipCode', '');
      }
      setFieldValue(name, null);
    },
    [setFieldValue]
  );

  const handleChange = useCallback(
    (e) => {
      const { name, value } = e.target;
      if (name === 'city') {
        setValues({
          ...values,
          city: value,
          zipCode: value?.zipCode || '',
          country: value?.country,
        });
      } else if (name === 'country') {
        setValues({
          ...values,
          country: value,
          city: '',
          zipCode: '',
        });
      } else {
        handleChangeFormik(e);
      }
    },
    [handleChangeFormik, values, setValues]
  );

  const handleCitySearchChange = useCallback((e) => {
    setCitySearchInput(e.target.value);
  }, []);

  const handleLoadMoreCities = useCallback(() => {
    if (!values.country) return;
    loadMoreCities({ countryId: values.country });
  }, [values.country, loadMoreCities]);

  useEffect(() => {
    if (values.country) {
      debouncedFetchCities(
        { countryId: values.country, search: citySearchInput },
        { reset: true }
      );
    }
  }, [citySearchInput, debouncedFetchCities, values.country]);

  useEffect(() => {
    EshopAPI.fetchCountries().then((countries) => {
      setCountries(
        countries.map((country) => ({
          label: country.name,
          value: country.id,
        }))
      );
    });
  }, []);

  const zipCode = { span: 4, component: 'zipCode' };
  const city = { span: 8, component: 'city' };

  const layout = [
    { span: 12, component: 'address' },
    { span: 12, component: 'addressComplement' },
    { span: 12, component: 'country' },
    ...(config.isZipOnLeftOnEshop ? [zipCode, city] : [city, zipCode]),
  ];

  if (loading) {
    return <Loader />;
  }

  return (
    <Grid container>
      <Grid item xs={12} md={3}></Grid>
      <Grid item xs={12} md={6}>
        <AddressForm
          cities={cities}
          countries={countries}
          values={values}
          errors={errors}
          disabled={saving}
          layout={layout}
          fetchingCities={fetchingCities}
          onSubmit={handleSubmit}
          onChange={handleChange}
          onClear={handleClear}
          onLoadMoreCities={handleLoadMoreCities}
          handleCitySearchChange={handleCitySearchChange}
        />
      </Grid>
      <Grid item xs={12} md={3}></Grid>
    </Grid>
  );
}

export default AddressFormContainer;
